.form {
    &-group {
        width: 100%;

        .v-select {
            flex: 1 1 auto;
            margin-bottom: 0;
            min-width: 0;
            position: relative;
            width: 1%;
        }
    }
}

.input {
    &-group {
        & > .v-select:not(:first-child) {
            .vs__dropdown-toggle {
                border: 1px solid $border;
                border-radius: 0 .25rem .25rem 0;
                padding: 4px 0 5px;
            }
        }
    }
}

.current-page {
    width: 60px;
}

.date-picker {
    display: flex;
    flex-flow: column;

    &-wrap {
        position: relative;
        flex-grow: 1;
    }

    &-input {
        padding-left: 2rem;
        margin-bottom: 10px;
    }

    svg {
        position: absolute;
        width: 1rem;
        height: 37px;
    }
}

.vs__clear {
    svg {
        fill: $blue;
    }
}

.vs__selected {
    margin: 2px;
}

.vs__actions {
    padding-top: 0;
    padding-right: 12px;
}

.vs__dropdown-text {
    position: relative;

    em {
        position: absolute;
        right: 0;
        top: 0;
        min-width: 55px;
        text-align: right;

        &:before {
            content: '$';
            left: 0;
            top: 0;
            position: absolute;
        }
    }
}
